import React from "react";
import Particles from 'react-particles-js';

class Background extends React.Component {
    render () {
        return (
            <Particles 
                //Background Particles effect
                params={{
                    "particles": {
                        "number": {
                            "value": 75
                        },
                        "size": {
                            "value": 3
                        }
                    },
                    "interactivity": {
                        "events": {
                            "onhover": {
                                "enable": true,
                                "mode": "repulse"
                            }
                        }
                    }
                }}
            
            />
        )
    }
}

export default Background;